import { Component, OnInit } from '@angular/core';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { Message, ConfirmationService } from 'primeng/primeng';
import { FileTemplateBean } from '../bean/file-template.bean';
import { tableMessage } from '../../../../../@uisftech/common/constant/config';
import { FileCenterHttpService } from '../http/file-center.http.service';
import { TZB_CUS_DOWNLOAD, TZB_CUS_UPLOAD } from '../../../../../@uisftech/common/service/http.constant';

@Component({
    selector: 'file-template',
    templateUrl: './file-template.html',
    styleUrls: ['./file-template.scss'],
    providers: [FileCenterHttpService,ConfirmationService]
})

export class FileTemplateComponent implements OnInit {
    msgs: Message[] = [];//信息提示框
    flag: boolean = true;//判断是否是初始化
    fileTemplateBean: FileTemplateBean = new FileTemplateBean();//文件模板信息
    tabMesg: string = tableMessage;//表格没有数据时的展示内容
    list: any = [];//假数据
    code: any;//获取所有码值
    total: number;
    first: number = 0;//首页
    FileTemplateCate: any = [];
    DtType: any = [];
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;
    UPLOAD: string = TZB_CUS_UPLOAD;
    FiledStatus:any=[];
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public fileCenterHttpService: FileCenterHttpService,
        public confirmationService:ConfirmationService,
    ) {
        this.codeValues();
        this.FileTemplateCate = this.code['FileTemplateCate'];//模板大类
        this.FiledStatus=this.code['FiledStatus'];//状态
        this.DtType=this.code['DTTYPE'];//模板类型
    }

    ngOnInit() {
        this.fileTemplateBean.pageSize = 10;
        this.fileTemplateBean.pageNum = 1;
        this.query();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //查询
    query() {
        this.fileTemplateBean.pageSize = 10;
        this.fileTemplateBean.pageNum = 1;
        this.queryClick();
    }

    queryClick() {
        this.fileCenterHttpService.findDocumentTemplate(this.fileTemplateBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.total = data.totalNum;
                this.list = data.templateModleList;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.fileTemplateBean.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        this.fileTemplateBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.fileTemplateBean.pageSize;
        this.queryClick();//调用查询的方法
    }
    //新增模板
    addTemplate() {
        this.router.navigate(['/pages/tzb/custom/file-center/file-template-create']);
    }

    //查看
    view(item) {
        this.router.navigate(['/pages/tzb/custom/file-center/file-template-detail', item]);
    }

    //下载
    download(item) {
        let param = {
            dtId: item.dtId
        }
        this.fileCenterHttpService.documentDownload(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                window.open(this.DOWNLOAD + "?fileName=" + encodeURI(data.fileName) + "&fileUrl=" + data.fileUrl, "_self")
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        })
    }

    //修改
    update(item) {
        this.router.navigate(['/pages/tzb/custom/file-center/file-template-create', item], { queryParams: { flag: '1' } });
    }

    //删除
    delete(item) {
           //删除
        this.confirmationService.confirm({
            message: '确定要删除'+item.dtId+'吗?',
            accept: () => {
                let param = {
                    dtId: item.dtId,
                    dtStatusId:'1',
                }
                this.fileCenterHttpService.updateDocumentTemplate(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
                })
            }
        });
    

    }

}