import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { FileCenterHttpService } from '../http/file-center.http.service';
import { FiledListBean } from './bean/field-list.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'field-list',
    templateUrl: './field-list.component.html',
    styleUrls: ['./field-list.component.scss'],
    providers: [ConfirmationService, FileCenterHttpService]
})

export class FieldListComponent implements OnInit {
    addDisplay: boolean = false;
    mesg: any;//报文头信息
    msgs: Message[] = [];//信息提示框
    showFlag: string;
    headerTitle: string;
    filedListBean: FiledListBean = new FiledListBean();
    list: any = [];
    total: number;
    first: number = 0;//首页
    code: any;
    FiledType: any = [];//字段类型
    FiledView: any = [];//界面展示形式
    FiledStatus: any = [];//界面展示形式
    inValue1: any;
    inValue2: any;
    constructor(
        public confirmationService: ConfirmationService,
        public fileCenterHttpService: FileCenterHttpService,
        public commfunc: Commfunc
    ) {
        this.codeValues();
        this.FiledView = this.code['filedView1'];//界面展示形式
        this.FiledStatus = this.code['FiledStatus'];//状态
        this.FiledType = this.code['FiledType'];//字段类型
    }

    ngOnInit() {
        this.filedListBean.pageNum = 1;
        this.filedListBean.pageSize = 10;
        this.query()
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //查询
    query() {
        this.filedListBean.pageNum = 1;
        this.filedListBean.pageSize = 10;
        this.queryClick()
    }
    queryClick() {
        this.fileCenterHttpService.queryAttrItemList(this.filedListBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.total = data.totalNum;
                this.list = data.fieldsList;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //重置
    reset() {
        this.filedListBean = new FiledListBean();
        this.filedListBean.pageNum = 1;
        this.filedListBean.pageSize = 10;
        this.querySecleReset();
    }
    //页码重置
    querySecleReset() {
        this.filedListBean.pageSize = 10;
        this.filedListBean.pageNum = 1;
        this.first = 0;
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.filedListBean.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        this.filedListBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.filedListBean.pageSize;
        this.queryClick();//调用查询的方法
    }
    delete(item) {
        this.confirmationService.confirm({
            message: '确定要停用'+item.daField+'吗?',
            accept: () => {
                let param = {
                    daField: item.daField,
                    filedStatus:'1',
                }
                this.fileCenterHttpService.createOrStoreAttrItem(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
                })
            }
        });
    }
    //新增
    add() {
        this.addDisplay = true;
        this.showFlag = '1';
        this.headerTitle = '字段列表新增';
    }
    //新增回调
    outValueAdd(param) {
        this.addDisplay = param;
        this.queryClick();
    }
    //修改
    update(item) {
        this.addDisplay = true;
        this.showFlag = '2';
        this.headerTitle = '字段列表修改';
        this.inValue2 = item.daField;
    }

    //修改回调
    outValueUpdate(event) {
        this.addDisplay = event;
        this.queryClick();
    }

    //详情
    check(item) {
        this.addDisplay = true;
        this.showFlag = '3';
        this.headerTitle = '字段列表详情';
        this.inValue1 = item.daField;
    }

    //启用
    toNewUse (item) {
        item['filedStatus']='0';
        this.fileCenterHttpService.createOrStoreAttrItem(item).subscribe(data=>{
            if (data.returnCode.code == 'success') {
                this.queryClick();
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
}