import { Component, OnInit } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { Message, MenuItem, SelectItem } from 'primeng/primeng';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { tableMessage } from '../../../../../../@uisftech/common/constant/config';
import { FileTemplateBean } from '../../bean/file-template.bean';
import * as _ from 'lodash';
import { FileCenterHttpService } from '../../http/file-center.http.service';

@Component({
	selector: 'file-template-detail',
	templateUrl: './file-template-detail.component.html',
	styleUrls: ['./file-template-detail.component.scss'],
	providers:[FileCenterHttpService]
})
/**
 * 文件模板详情
 */
export class FileTemplateDetailComponent implements OnInit {

	mesg: any;//报文头信息
	msgs: Message[] = [];//信息提示框
	tabMesg: string = tableMessage;//表格没有数据时的展示内容
	fileTemplateBean: FileTemplateBean = new FileTemplateBean();//文件模板信息
	fileType: any;//文件类型
	items: MenuItem[];//下一步
	activeIndex: number;
	vlaue: any;//显示哪个div
	flag: number;//判断重置
	Flag: boolean = false;//判断重置
	code: any;//码值
	oneChooseList: any = [];//单选-数组
	allChooseList: boolean = false;//全选
	detailDisplay: boolean = false;//规则详情展示
	detail: any = {};//详情展示
	arr: any = [];//假数据
	tableList1: any = [];//假数据-左边表格
	tableList2: any = [];//假数据-右边表格
	aaa: any;
	bbb: SelectItem[];
	ccc: any = [];
	dtId:string;//模板id
	dataMap:any={};
	dataList1:any=[];
	dataList2:any=[];
	dataList3:any=[];
	Judge:any=[];//判断是否
	FileTemplateCate:any=[];
	DtType:any=[];
	FiledType:any=[];
	constructor(
		public commfunc: Commfunc,
		public router: Router,
		public fileCenterHttpService:FileCenterHttpService,
		public activatedRoute:ActivatedRoute
	) {
		this.mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		this.codeValues() //调用方法，获取全部码值
		this.Judge=this.code['JUDGE'];//判断是否
		this.FileTemplateCate=this.code['FileTemplateCate'];//模板大类
		this.DtType=this.code['DTTYPE'];//模板类型
		this.FiledType=this.code['FiledType'];//字段类型
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	ngOnInit() {
		this.dtId = this.activatedRoute.snapshot.params['dtId'];
		this.echo();
	}


	//详情
	echo () {
		let param = {
			dtId:this.dtId
		}
		this.fileCenterHttpService.findDocumentTemplateDetails(param).subscribe(data=>{
			if (data.returnCode.code == 'success') {
				this.dataMap=data.docuTem;
				this.dataList2=data.docuTem.rules;
				this.dataList1=data.docuTem.attrs;
				this.dataList3=data.docuTem.useRanges;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        },error=>{
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail:'调用服务失败'});
        })
	}

	//返回
	returnBtn() {
		setTimeout(() => {
			window.history.go(-1);
		}, 500)
	}


}