import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { Message } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
@Component({
    selector: 'rule-info-detail',
    templateUrl: './rule-info-detail.component.html',
    styleUrls: ['./rule-info-detail.component.scss'],
})
// 字段详情

export class RuleInfoDeatilComponent implements OnInit {
    @Input() inValue: any;
    mesg: any;//报文头信息
    msgs: Message[] = [];//信息提示框
    items: MenuItem[];//下一步
    activeIndex: number;
    vlaue: any;//显示哪个div
    tableList2: any = [];
    detail: any = {};
    code:any;
    Judge:any=[];
    constructor(
        public commfunc: Commfunc,
        public router: Router,
    ) {
        this.codeValues();
        this.Judge = this.code['JUDGE'];//判断是否
    }

    ngOnInit() {
        this.detail = this.inValue;

    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

}