import { Component, OnInit } from '@angular/core';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { RuleInfoBean } from '../bean/rule-info.bean';
import { tableMessage } from '../../../../../@uisftech/common/constant/config';
import { Message, ConfirmationService } from 'primeng/primeng';
import { FileCenterHttpService } from '../http/file-center.http.service';
@Component({
    selector: 'rule-info',
    templateUrl: './rule-info.component.html',
    styleUrls: ['./rule-info.component.scss'],
    providers: [ConfirmationService, FileCenterHttpService]
})
// 规则信息
export class RuleInfoComponent implements OnInit {
    tabMesg: string = tableMessage;//表格没有数据时的展示内容
    ruleInfoBean: RuleInfoBean = new RuleInfoBean();//规则信息
    msgs: Message[] = [];//信息提示框
    list: any = [];//假数据
    detailDisplay: boolean = false;//查看模态框
    addDisplay: boolean = false; //增加模态框
    updateDisplay: boolean = false;//修改模态框
    currentPage: any;//当前页
    rows: any;//当前显示条数
    first: number = 0;//首页
    total: any; //总页数
    inValue: any;
    inValueDetail: any;
    code:any;
    Judge:any=[];
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public fileCenterHttpService: FileCenterHttpService,
        public confirmationService: ConfirmationService
    ) {
        this.codeValues();
        this.Judge=this.code['JUDGE'];//判断是否
    }

    ngOnInit() {
        this.ruleInfoBean.pageNum = 1;
        this.ruleInfoBean.pageSize = 10;
        this.query();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    //查询
    query() {
        this.ruleInfoBean.pageNum = 1;
        this.ruleInfoBean.pageSize = 10;
        this.queryClick();

    }

    queryClick() {
        this.fileCenterHttpService.findDocumentRules(this.ruleInfoBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.total = data.totalNum;
                this.list = data.rulesList;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //重置
    reset() {
        this.ruleInfoBean = new RuleInfoBean();
        this.ruleInfoBean.pageNum = 1;
        this.ruleInfoBean.pageSize = 10;
        this.querySecleReset();
    }
    //页码重置
    querySecleReset() {
        this.ruleInfoBean.pageSize = 10;
        this.ruleInfoBean.pageNum = 1;
        this.first = 0;
    }
    //新增
    add() {
        this.addDisplay = true;
    }

    //增加回调
    addCall(event) {
        this.addDisplay = event;
        this.queryClick();
    }

    //查看
    check(col) {
        this.detailDisplay = true;
        this.inValueDetail = col;
    }

    //修改
    update(col) {
        this.updateDisplay = true;
        this.inValue = col;
    }
    //修改 返回信息
    updateCall(event) {
        this.updateDisplay = event;
        this.queryClick();
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.ruleInfoBean.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        this.ruleInfoBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.ruleInfoBean.pageSize;
        this.queryClick();//调用查询的方法
    }

    //删除
    detele(item) {
        this.confirmationService.confirm({
            message: '是否确定删除选中的规则信息?',
            accept: () => {
                let param = {
                    drId: item.drId,
                }
                this.fileCenterHttpService.deleteDocumentRules(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
                })
            }
        });
    }
}