import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { Message, MenuItem, SelectItem } from 'primeng/primeng';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';
import { tableMessage } from '../../../../../../../@uisftech/common/constant/config';
import * as _ from 'lodash';
import { FileCenterHttpService } from '../../../http/file-center.http.service';
import { FileTemplateCreateBean } from '../bean/file-template-create.bean';
import { FiledListBean } from '../../../field-list/bean/field-list.bean';

@Component({
    selector: 'cust-choose',
    templateUrl: './cust-choose.component.html',
    styleUrls: ['./cust-choose.component.scss']
})

export class CustChooseComponent implements OnInit {
    addDisplay: boolean=false;
    @Input() inValue: any;
    @Output() outValue = new EventEmitter();
    tabMesg: string = tableMessage;//表格没有数据时的展示内容
    tableList2: any = [];
    checkAll: boolean = false;
    checkBox: any = [];
    chooseData: any = [];
    tableData: any = [];
    dtId: string;
    msgs: Message[] = [];//信息提示框
    first: number = 0;//首页
    filedListBean: FiledListBean = new FiledListBean();
    total: number;
    inValue1: any;
    code:any;
    FiledType:any=[];
    FiledView:any=[];
    FiledStatus:any=[];
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public fileCenterHttpService: FileCenterHttpService
    ) {
        this.codeValues();
        this.FiledType = this.code['FiledType'];//字段类型
        this.FiledView = this.code['filedView1'];//界面展示形式
        this.FiledStatus = this.code['FiledStatus'];//状态
    }

    ngOnInit() {
        this.dtId = this.inValue;
        this.filedListBean.pageNum = 1;
        this.filedListBean.pageSize = 10;
        this.query();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    query() {
        this.filedListBean.pageNum = 1;
        this.filedListBean.pageSize = 10;
        this.queryClick();
    }
    queryClick() {
        let param = {
            dtId:this.dtId,
            daField:this.filedListBean.daField,
            daName:this.filedListBean.daName,
            description:this.filedListBean.description,
            pageNum:this.filedListBean.pageNum,
            pageSize:this.filedListBean.pageSize
        }
        this.fileCenterHttpService.queryUnusedAttrItem(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.fieldsList;
                this.total = data.totalNum
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })

    }
    //详情
    view(item) {
        this.addDisplay=true;
        this.inValue1 = item.daField;
    }
    // 单选按钮
    checkBoxClick(i, data) {
        if (data.checkBox) {
            this.chooseData.push(data)
        } else {
            this.chooseData.forEach((item, i) => {
                if (item == data) {
                    this.chooseData.splice(i, 1)
                }
            });
        }
        if (this.chooseData.length == this.tableData.length) {
            this.checkAll = true;
        } else {
            this.checkAll = false;
        }
    }
    // 全选按钮
    checkAllClick() {
        if (this.tableData) {
            for (let i = 0; i < this.tableData.length; i++) {
                if (this.checkAll == true) {
                    this.tableData[i].checkBox = true;
                    this.chooseData.push(this.tableData[i]);
                }
                else {
                    this.chooseData = [];
                    this.tableData[i].checkBox = false;
                }
            }
        }
    }
    paginate(event) {
        //每页显示的条数
        this.filedListBean.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        this.filedListBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.filedListBean.pageSize;
        this.queryClick();//调用查询的方法
    }
    reset() {
        this.filedListBean = new FiledListBean();
        this.filedListBean.pageNum = 1;
        this.filedListBean.pageSize = 10;
        this.querySecleReset();
    }
    //页码重置
    querySecleReset() {
        this.filedListBean.pageSize = 10;
        this.filedListBean.pageNum = 1;
        this.first = 0;
    }
    //选择
    selectClick() {
        this.outValue.emit(this.chooseData);
    }
    //取消
    cancelClick() {
        this.outValue.emit(false);
    }
}