import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Message, MenuItem, SelectItem } from 'primeng/primeng';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { tableMessage } from '../../../../../../@uisftech/common/constant/config';
import { FileTemplateCreateBean } from './bean/file-template-create.bean';
import * as _ from 'lodash';
import { FileCenterHttpService } from '../../http/file-center.http.service';

@Component({
	selector: 'file-template-create',
	templateUrl: './file-template-create.component.html',
	styleUrls: ['./file-template-create.component.scss'],
	providers: [FileCenterHttpService]
})
//文件模板新增
export class FileTemplateCreateComponent implements OnInit {

	mesg: any;//报文头信息
	msgs: Message[] = [];//信息提示框
	tabMesg: string = tableMessage;//表格没有数据时的展示内容
	fileTemplateCreateBean: FileTemplateCreateBean = new FileTemplateCreateBean();//文件模板信息
	fileType: any;//文件类型
	items: MenuItem[];//下一步
	itemes: MenuItem[];//下一步
	activeIndex: number;
	activeIndexs: number;
	vlaue: any;//显示哪个div
	flag: number;//判断重置
	Flag: boolean = false;//判断重置
	code: any;//码值
	oneChooseList: any = [];//单选-数组
	allChooseList: boolean = false;//全选
	detailDisplay: boolean = false;//规则详情展示
	detail: any = {};//详情展示
	custChooseDisplay: boolean = false;
	list: any = [];//假数据
	arr: any = [];//假数据
	tableList1: any = [];//假数据-左边表格
	tableList2: any = [];//假数据-右边表格
	FileTemplateCate: any = [];//上传文件模板大类
	FileTemplateCates: any = [];//展示模板大类
	DtType: any = [];//模板类型
	flagUpdate: string;
	dtId: string;//模板编号
	inValue: any;
	FiledType: any = [];
	Judge: any = [];
	drName: string;
	drEntityField: string;
	checkBox: any = [];
	checkBox1: any = [];
	chooseData: any = [];
	chooseData1: any = [];
	aa: any;
	showStep: boolean = true;//下一步调接口
	compWidth: string = '16%';
	constructor(
		public commfunc: Commfunc,
		public router: Router,
		public activatedRoute: ActivatedRoute,
		public fileCenterHttpService: FileCenterHttpService
	) {
		this.mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		this.codeValues() //调用方法，获取全部码值
		this.DtType = this.code['DtType'];//模板类型
		this.FileTemplateCate = this.code['FileTemplateCate'];//上传文件模板大类
		this.FileTemplateCates = this.code['FileTemplateCates'];//展示模板大类
		this.FiledType = this.code['FiledType'];//字段类型
		this.Judge = this.code['JUDGE'];//判断是否
		activatedRoute.queryParams.subscribe(queryParams => {
			this.flagUpdate = queryParams.flag;
		})
		if (this.flagUpdate) {
			this.dtId = this.activatedRoute.snapshot.params['dtId']
		}
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	ngOnInit() {
		this.activeIndex = 0;
		this.activeIndexs = 0;
		this.vlaue = '0';
		this.flag = 0;
		this.items = [
			{
				label: '模板基本信息配置',
				command: (event: any) => {
					this.activeIndex = 0;
				}
			},
			{
				label: '模板配置',
				command: (event: any) => {
					this.activeIndex = 1;
				}
			},
			{
				label: '模板规则配置',
				command: (event: any) => {
					this.activeIndex = 2;
				}
			},
			{
				label: '文件适用范围配置',
				command: (event: any) => {
					this.activeIndex = 3;
				}
			}
		];
		this.itemes = [
			{
				label: '模板基本信息配置',
				command: (event: any) => {
					this.activeIndexs = 0;
				}
			},
			{
				label: '模板配置',
				command: (event: any) => {
					this.activeIndexs = 1;
				}
			},
		];
		if (this.flagUpdate == '1') {
			this.updateDetail();
		}
		this.fileTemplateCreateBean.dtFiletype = '.xls';//单选框初始化-excel文件类型
		this.fileTemplateCreateBean.dtType = '03';//模板类型
	}

	//添加模板项
	addTemplate() {
		this.custChooseDisplay = true;
		this.inValue = this.dtId;

	}

	//删除
	delete(col, i) {
		// console.log(col);
		this.list.splice(this.list[i], 1);
		this.list = _.clone(this.list);
	}

	toRuleInfo(col) {
		this.detailDisplay = true;
		this.detail = col;
	}
	// 单选按钮-左边
	choose(i, data) {
		if (this.checkBox) {
			this.chooseData.push(data)
		} else {
			this.chooseData.forEach((item, i) => {
				if (item == data) {
					this.chooseData.splice(i, 1)
				}
			});
		}
	}
	//左-全移
	leftMoveAll() {
		let stu = true;
		let temp = [];
		let temp1 = [];
		let arrsright = [];
		this.tableList2.forEach((item, i) => {
			if (stu) {
				arrsright.push(i)
				arrsright.sort(function (a, b) { return b - a; })
				this.tableList1.push(this.tableList2[i])
			}
		});
		if (stu) {
			arrsright.forEach((item, i) => {
				this.tableList2.splice(item, 1)
			});
			temp = _.clone(this.tableList1);
			this.tableList1 = _.clone(temp);
			temp1 = _.clone(this.tableList2);
			this.tableList2 = _.clone(temp1);
			this.checkBox = [];
		}
	}

	//左移
	leftMove() {
		let temp = [];
		let temp1 = [];
		let arrsleft = [];
		this.checkBox1.forEach(item => {
			arrsleft.push(item)
			arrsleft.sort(function (a, b) { return b - a; })
			this.tableList1.push(this.tableList2[item])
		});
		arrsleft.forEach((item, i) => {
			this.tableList2.splice(item, 1)
		});
		temp = _.clone(this.tableList2)
		this.tableList2 = _.clone(temp)
		temp1 = _.clone(this.tableList1)
		this.tableList1 = _.clone(temp1)
		this.checkBox1 = [];
	}

	//右移
	rightMove() {
		let stu = true;
		let temp = [];
		let temp1 = [];
		let arrsright = [];
		this.checkBox.forEach((item, i) => {
			this.tableList2.forEach(item_2 => {
				if (item_2.drId == this.tableList1[item].drId) {
					stu = false;
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: "请不要选择重复组件" });
					this.checkBox = [];
				}
			})
			if (stu) {
				arrsright.push(item)
				arrsright.sort(function (a, b) { return b - a; })
				this.tableList2.push(this.tableList1[item])
			}
		});
		if (stu) {
			arrsright.forEach((item, i) => {
				this.tableList1.splice(item, 1)
			});
			temp = _.clone(this.tableList2);
			this.tableList2 = _.clone(temp);
			temp1 = _.clone(this.tableList1);
			this.tableList1 = _.clone(temp1);
			this.checkBox = [];
		}
	}
	//右-全移
	rightMoveAll() {
		let stu = true;
		let temp = [];
		let temp1 = [];
		let arrsright = [];
		this.tableList1.forEach((item, i) => {
			this.tableList2.forEach(item_2 => {
				if (item_2.drId == this.tableList1[i].drId) {
					stu = false;
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: "请不要选择重复组件" });
					this.checkBox = [];
				}
			})
			if (stu) {
				arrsright.push(i)
				arrsright.sort(function (a, b) { return b - a; })
				this.tableList2.push(this.tableList1[i])
			}
		});
		if (stu) {
			arrsright.forEach((item, i) => {
				this.tableList1.splice(item, 1)
			});
			temp = _.clone(this.tableList2);
			this.tableList2 = _.clone(temp);
			temp1 = _.clone(this.tableList1);
			this.tableList1 = _.clone(temp1);
			this.checkBox = [];
		}

	}

	//重置
	reset() {
		if (this.activeIndex == 0) {
			this.fileTemplateCreateBean.dtName = '';
			this.fileTemplateCreateBean.fileTemplateCate = '';
			this.fileTemplateCreateBean.dtDescription = '';
			this.fileTemplateCreateBean.dtEntity = '';
		} else if (this.activeIndex == 1) {
			this.list.forEach(element => {
				element.daWilllose = '';
				element.daUpdate = '';
				element.daQuery = '';
				element.daAnotherField = '';
				element.daEntityField = '';
				element.daFieldMapping = '';
				element.daDescription = '';
			});
		} else if (this.activeIndex == 2) {
			this.drName = '';
			this.drEntityField = '';
			this.leftClick();
			this.rightClick();
		}
	}

	//返回
	returnBtn() {
		setTimeout(() => {
			window.history.go(-1);
		}, 500)
	}

	//上一步
	prev() {
		if (this.fileTemplateCreateBean.dtType == '03') {
			if (this.activeIndex == 1) {
				this.activeIndex = this.activeIndex - 1;
				this.vlaue = this.activeIndex;
				this.showStep = false;
			} else {
				this.activeIndex = this.activeIndex - 1;
				this.vlaue = this.activeIndex;
			}
		} else if (this.fileTemplateCreateBean.dtType == '04') {
			if (this.activeIndexs == 1) {
				this.activeIndexs = this.activeIndexs - 1;
				this.vlaue = this.activeIndexs;
				this.showStep = false;
			}
		}
	}

	//下一步
	next() {
		if (this.fileTemplateCreateBean.dtType != '04') {
			if (this.activeIndex == 0) {
				if (this.flagUpdate || !this.showStep) {
					if (!this.fileTemplateCreateBean.dtName) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '模板名称不能为空' });
						return;
					}
					if (!this.fileTemplateCreateBean.fileTemplateCate) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '模板大类不能为空' });
						return;
					}
					if (!this.fileTemplateCreateBean.dtEntity) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '实体文件名不能为空' });
						return;
					}
					this.fileTemplateCreateBean.dtId = this.dtId;
					this.fileCenterHttpService.updateDocumentTemplate(this.fileTemplateCreateBean).subscribe(data => {
						if (data.returnCode.code == 'success') {
							this.activeIndex = this.activeIndex + 1;
							this.vlaue = this.activeIndex;
							this.temPlateClick();
							this.msgs = [];
							this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
						}
					}, error => {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
					})

				} else {
					if (!this.fileTemplateCreateBean.dtName) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '模板名称不能为空' });
						return;
					}
					if (!this.fileTemplateCreateBean.fileTemplateCate) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '模板大类不能为空' });
						return;
					}
					if (!this.fileTemplateCreateBean.dtEntity) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '实体文件名不能为空' });
						return;
					}
					this.fileCenterHttpService.createDocumentTemplate(this.fileTemplateCreateBean).subscribe(data => {
						if (data.returnCode.code == 'success') {
							this.dtId = data.dtId;
							this.activeIndex = this.activeIndex + 1;
							this.vlaue = this.activeIndex;
							this.temPlateClick();
							this.msgs = [];
							this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
						}
					}, error => {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
					})
				}
			} else if (this.activeIndex == 1) {//模板配置
				let arr = [];
				let obj = {};
				this.list.forEach((element, index) => {
					element['dtId'] = this.dtId;
					element['daNo'] = index + 1;
				});
				let param = {
					daList: this.list
				}
				this.fileCenterHttpService.createDocumentAttributes(param).subscribe(data => {
					if (data.returnCode.code == 'success') {
						this.activeIndex = this.activeIndex + 1;
						this.vlaue = this.activeIndex;
						this.leftClick();
						this.rightClick();
						this.msgs = [];
						this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
					} else {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
					}
				}, error => {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
				})
			} else if (this.activeIndex == 2) {//模板规则配置
				let arr = [];
				this.tableList2.forEach(element => {
					arr.push(element.drId);

				});
				let param = {
					dtId: this.dtId,
					drId: arr.toString(),
				}
				if (this.tableList2.length > 0) {
					this.fileCenterHttpService.createDocumentRulesRelationship(param).subscribe(data => {
						if (data.returnCode.code == 'success') {
							this.activeIndex = this.activeIndex + 1;
							this.vlaue = this.activeIndex;
							this.msgs = [];
							this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
						}
					}, error => {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
					})
				} else if (this.tableList2.length == 0) {
					this.activeIndex = this.activeIndex + 1;
					this.vlaue = this.activeIndex;
				}

			} else if (this.activeIndex == 3) {

			}
		} else if (this.fileTemplateCreateBean.dtType == '04') {
			if (this.activeIndexs == 0) {
				if (this.flagUpdate || !this.showStep) {
					if (!this.fileTemplateCreateBean.dtName) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '模板名称不能为空' });
						return;
					}
					if (!this.fileTemplateCreateBean.fileTemplateCate) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '模板大类不能为空' });
						return;
					}
					if (!this.fileTemplateCreateBean.dtEntity) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '实体名不能为空' });
						return;
					}
					this.fileTemplateCreateBean.dtId = this.dtId;
					this.fileCenterHttpService.updateDocumentTemplate(this.fileTemplateCreateBean).subscribe(data => {
						if (data.returnCode.code == 'success') {
							this.activeIndexs = this.activeIndexs + 1;
							this.vlaue = this.activeIndexs;
							this.temPlateClick();
							this.msgs = [];
							this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
						}
					}, error => {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
					})

				} else {
					if (!this.fileTemplateCreateBean.dtName) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '模板名称不能为空' });
						return;
					}
					if (!this.fileTemplateCreateBean.fileTemplateCate) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '模板大类不能为空' });
						return;
					}
					if (!this.fileTemplateCreateBean.dtEntity) {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '实体名不能为空' });
						return;
					}
					this.fileCenterHttpService.createDocumentTemplate(this.fileTemplateCreateBean).subscribe(data => {
						if (data.returnCode.code == 'success') {
							this.dtId = data.dtId;
							this.activeIndexs = this.activeIndexs + 1;
							this.vlaue = this.activeIndexs;
							this.temPlateClick();
							this.msgs = [];
							this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
						}
					}, error => {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
					})
				}
			} else if (this.activeIndexs == 1) {//模板配置
				let arr = [];
				let obj = {};
				this.list.forEach((element, index) => {
					element['dtId'] = this.dtId;
					element['daNo'] = index + 1;
				});
				let param = {
					daList: this.list
				}
				this.fileCenterHttpService.createDocumentAttributes(param).subscribe(data => {
					if (data.returnCode.code == 'success') {
						// this.activeIndexs = this.activeIndexs + 1;
						// this.vlaue = this.activeIndexs;
						// this.leftClick();
						// this.rightClick();
						this.msgs = [];
						this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
					} else {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
					}
				}, error => {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
				})
			}
		}
	}

	//模板配置列表删除
	toDelete(i) {
		this.list.splice(i, 1);
		this.list = _.clone(this.list);
	}
	//模板配置列表查询
	temPlateClick() {
		let param = {
			dtId: this.dtId
		}
		this.fileCenterHttpService.findDocumentAttributes(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				if (data.resultList) {
					this.list = data.resultList;
				} else {
					this.list = [];
				}
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	//保存
	save() {
		let param = {
			dtId: this.dtId,
		}
		this.fileCenterHttpService.createOrStoreDocumentUseRange(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}

	//左边-可使用
	leftClick() {
		let param = {
			dtId: this.dtId,
			drEntityField: this.drEntityField,
			drName: this.drName
		}
		this.fileCenterHttpService.queryDocumentRulesUnusedAssociation(param).subscribe(data => {//左边
			if (data.returnCode.code == 'success') {
				this.tableList1 = data.ruleList;
				// this.tableList1 = this.commfunc.listToLabelvalue(data.ruleList, "drName", "drId");
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	//右边-已使用
	rightClick() {
		this.fileCenterHttpService.queryDocumentRulesAssociation({ dtId: this.dtId }).subscribe(data => {//左边
			if (data.returnCode.code == 'success') {
				if (data.ruleList) {
					this.tableList2 = data.ruleList;
				} else {
					this.tableList2 = [];
				}
				// this.tableList2 = this.commfunc.listToLabelvalue(data.ruleList, "drName", "drId");
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}

	//字段选择
	outValue(event) {
		if (event == false) {
			this.custChooseDisplay = event;
		} else {
			event.forEach(element => {
				element['daType'] = element.filedType;
				element['daLength'] = element.filedLen;
			});
			console.log(...event);
			this.list.push(...event);
			let result = [];
			let obj = {};
			for (let i = 0; i < this.list.length; i++) {
				if (!obj[this.list[i].daField]) {
					result.push(this.list[i]);
					obj[this.list[i].daField] = true;
				}
			}
			this.list = _.clone(result);
			this.custChooseDisplay = false;
			if (this.list) {
				this.list.forEach(element => {
					element['daWilllose'] = '0';
					element['daUpdate'] = '0';
					element['daQuery'] = '0';
				});
			}
		}
	}

	getIndex(list, item) {
		let index = -1;
		for (let i = 0; i < list.length; i++) {
			if (list[i].daField == item.daField) {
				index = i;
				break;
			}
		}
		return index
	}
	//上
	up(car) {
		let carList = JSON.parse(JSON.stringify(this.list));
		let i = this.getIndex(carList, car);
		if (i != -1 && i != 0) {
			let a = carList[i - 1];
			carList[i] = a;
			carList[i - 1] = car;
		}
		this.list = carList;
	}

	//下
	down(car) {
		let carList = JSON.parse(JSON.stringify(this.list));
		let i = this.getIndex(carList, car);
		if (i < carList.length - 1) {
			let a = carList[i + 1];
			carList[i] = a;
			carList[i + 1] = car;
		}
		this.list = carList;
	}

	//修改调详情接口
	updateDetail() {
		this.fileCenterHttpService.findDocumentTemplateDetails({ dtId: this.dtId }).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.fileTemplateCreateBean = data.docuTem;
				this.list = data.docuTem.attrs;
				console.log(this.list)
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	change() {
		setTimeout(() => {
			let compWidth = document.getElementById('compId');
			let compLi = compWidth.getElementsByTagName('li');
			for (let i = 0; i < compLi.length; i++) {
				compLi[i].style.width = `${100 / compLi.length}%`;
			}
		})
	}
}