import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { addFieldBean } from '../bean/field-list.bean';
import { FileCenterHttpService } from '../../http/file-center.http.service';
import { checkZzNum } from '../../../../../../@uisftech/common/service/validator';
@Component({
    selector: 'field-list-add',
    templateUrl: './field-list-add.component.html',
    styleUrls: ['./field-list-add.component.scss'],
    providers: [ConfirmationService, FileCenterHttpService]
})

export class FieldListAddComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    mesg: any;//报文头信息
    addFieldBean: addFieldBean = new addFieldBean();//规则信息
    msgs: Message[] = [];//信息提示框
    items: MenuItem[];//下一步
    activeIndex: number;
    vlaue: any;//显示哪个div
    tableList2: any = [];
    //校验
    userform: FormGroup;
    code: any;
    FiledType: any = [];
    FiledView: any = [];
    englishField: string;//英文字段名称
    isShowTip: boolean = true;
    list:any=[];
    kk:string='日期型';
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
        public fb: FormBuilder,
        public fileCenterHttpService: FileCenterHttpService
    ) {
        this.userform = this.fb.group({
            "daField": new FormControl('', Validators.required),
            "daName": new FormControl('', Validators.required),
            "filedType": new FormControl('', Validators.required),
            "description": new FormControl(''),
            "filedLen": new FormControl('', Validators.required),
            "filedView": new FormControl('', Validators.required),
        });
        this.codeValues();
        this.FiledType = this.code['FiledType'];//字段类型
        this.FiledView = this.code['FiledView3'];//界面展示形式
        this.tableList2= [{
            fieldValue:'',
            description:''
        }];

    }

    ngOnInit() {
        this.addFieldBean.filedType = this.FiledType[0].value;
        this.addFieldBean.filedView = this.FiledView[0].value;
        this.activeIndex = 0;
        this.vlaue = '0';
        this.items = [
            {
                label: '字段基本信息配置',
                command: (event: any) => {
                    this.activeIndex = 0;
                }
            },
            {
                label: '字段取值配置',
                command: (event: any) => {
                    this.activeIndex = 1;
                }
            },
        ];

    }
    change () {
        if (this.addFieldBean.filedType=='03') {
            this.addFieldBean.filedView='03';
        }else{
            this.addFieldBean.filedView = this.FiledView[0].value;
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //下一步
    nextclick() {
        if (this.activeIndex == 0) {
            if (this.addFieldBean.filedType=='03') {
                this.addFieldBean.filedView='03';
            }
            this.addFieldBean.filedStatus = '0';
            this.fileCenterHttpService.createOrStoreAttrItem(this.addFieldBean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.isShowTip = false;
                    this.activeIndex = this.activeIndex + 1;
                    this.vlaue = this.activeIndex;
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        } else if (this.activeIndex == 1) {
        }
    }
    //下一步
    next () {
        this.isShowTip = true;
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        let param ={
            daField:this.addFieldBean.daField
        }
        this.fileCenterHttpService.queryAttrItemList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.list = data.fieldsList;
                if (data.fieldsList) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '字段英文名已存在' });
                    return;
                }else{
                    this.nextclick();             
                }
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //上一步
    prev() {
        this.activeIndex = this.activeIndex - 1;
        this.vlaue = this.activeIndex;
    }
    //删除
    delete(i) {
        this.tableList2.splice(i, 1);
        this.tableList2 = _.clone(this.tableList2);
    }
    //新增
    add() {
        let aa = _.clone(this.tableList2);
        aa.push({
            fieldValue: '', description: '',
        });
        this.tableList2 = aa;
    }
    cancel() {
        this.outValue.emit(false);
    }
    //保存查询
    queryClick() {
        if (this.addFieldBean.filedView == '02') {
            let param = {
                daField: this.addFieldBean.daField,
                itemVals: this.tableList2,
            }
            this.fileCenterHttpService.createOrStorAttrItemVal(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.outValue.emit(false);
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        } else {
            if (this.addFieldBean.filedType=='03') {
                this.addFieldBean.filedView='03';
            }
            this.addFieldBean.filedStatus = '0';
            this.fileCenterHttpService.createOrStoreAttrItem(this.addFieldBean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.isShowTip = false;
                    this.outValue.emit(false);
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        }
    }

    //保存
    saveClick() {
        this.isShowTip = true;
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        if (this.addFieldBean.filedView != '02') {
            let param ={
                daField:this.addFieldBean.daField
            }
            this.fileCenterHttpService.queryAttrItemList(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.list = data.fieldsList;
                    if (data.fieldsList) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '字段英文名已存在' });
                        return;
                    }else{
                        this.queryClick();             
                    }
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        }else{
            this.queryClick();  
        }
    }
    rest() {
        if (this.activeIndex == 0) {
            this.isShowTip = false;
            this.addFieldBean = new addFieldBean();
            this.addFieldBean.filedStatus = '0';
            this.addFieldBean.filedType = this.FiledType[0].value;
            this.addFieldBean.filedView = this.FiledView[0].value;
        } else if (this.activeIndex == 1) {
            this.tableList2.forEach(element => {
                element.fieldValue = '';
                element.description = '';
            });
        }
    }
    //校验字段长度只能输入数字
    filedNum (event) {
        if (checkZzNum(null, event.target.value)) {
			this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字' }];
			event.target.value = '';
		}
    }
}