import { Component, OnInit, Output, EventEmitter, Input, OnChanges } from '@angular/core';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { Message } from 'primeng/primeng';
import { NewRuleBean } from '../../bean/rule-info.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { FileCenterHttpService } from '../../http/file-center.http.service';
@Component({
    selector: 'rule-info-update',
    templateUrl: './rule-info-update.component.html',
    styleUrls: ['./rule-info-update.component.scss']
})

export class RuleInfoUpdateComponent implements OnInit, OnChanges {
    @Output() outValue = new EventEmitter();
    @Input('in-value') inValue: any;
    msgs: Message[] = [];//信息提示框
    newRuleBean: NewRuleBean = new NewRuleBean();//规则信息
    //校验
    userform: FormGroup;
    drCodingOptions: any = [];
    code: any;
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public fileCenterHttpService: FileCenterHttpService,
        public fb: FormBuilder,
    ) {
    }
    ngOnChanges() {
        this.newRuleBean = this.inValue;
    }
    ngOnInit() {
        this.codeValues();
        this.drCodingOptions = this.code['drCodingType'];//规则编号
        this.userform = this.fb.group({
            // "drValue": new FormControl('', Validators.required),
            "drEntityField": new FormControl(''),
            "drName": new FormControl('', Validators.required),
            "drCoding": new FormControl('', Validators.required),
            "drServiceId": new FormControl('', Validators.required),
            "drServiceName": new FormControl('', Validators.required),
            "errorDescription": new FormControl('', Validators.required),
            "drDescription": new FormControl(''),
        });
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //提交
    submitClick() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        this.fileCenterHttpService.updateDocumentRules(this.newRuleBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }

    //取消
    cancel() {
        this.outValue.emit(false);
    }

}