import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { FileTemplateComponent } from './file-template/file-template';
import { RuleInfoComponent } from './rule-info/rule-info.component';
import { FileTemplateCreateComponent } from './file-template/file-template-create/file-template-create.component';
import { FileTemplateDetailComponent } from './file-template/file-template-detail/file-template-detail.component';
import { FieldListComponent } from './field-list/field-list.component';

const routes: Routes = [{
    path: '',
    children: [
        {
            path: 'rule-info',
            component: RuleInfoComponent
        },
        {
            path: 'file-template',
            component: FileTemplateComponent
        },
        {
            path: 'file-template-create',
            component: FileTemplateCreateComponent
        },
        {
            path: 'file-template-detail',
            component: FileTemplateDetailComponent
        },
        {
            path: 'field-list',
            component:FieldListComponent
        }
    ]
}]

export const FileCenterRouting: ModuleWithProviders = RouterModule.forChild(routes);